.bg{
    width: 100vw;
    height: 100vh;
    opacity: 0.1;
    position: relative;
}

.bg-container{
    position: absolute;
    left: 0;
    top: 0;
    background: #000;
    z-index: -1;
}

.name{
    color: #fff;
    font-size: 4rem;
    vertical-align: middle;
    font-weight: bolder;
}


.vertical-middle{
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    padding: 135px;
}

.profile-pic-container{
    width: 74%;
    margin-left: 13%;
    box-shadow: rgba(0, 255, 255, 0.49) 0 2px 7px 3px;
}

.summary{
    color: rgba(255, 255, 255, 0.84);
    font-size: 1.8rem;
    margin-bottom: 40px;
    white-space: pre-line;
}
